import { createStore } from 'vuex'

export default createStore({
        state: {
            signInState: false,
            accessToken: '',
            filesList: [],
            foldersList: [],
            selectedFile: '',
            selectedFolder: { name: 'root', id: 'appDataFolder' },
            previousFolders: [],
        },
        mutations: {
            setSignInState(state, bool) {
                console.log('updated', bool)
                state.signInState = bool
            },
            setAccessToken(state, arg) {
                console.log('updated', arg)
                state.accessToken = arg
            },
            setFilesList(state, list) {
                state.filesList = list
            },
            setFoldersList(state, list) {
                state.foldersList = list
            },
            setSelectedFile(state, name) {
                console.log(name, "name")
                state.selectedFile = state.filesList.filter(file => file.name == name)[0]
                    //console.log(state.selectedFile.name)
            },
            setSelectedFolder(state, arg) { //only one 'payload' allowed it seems; i.e., (state, para1, para2) not allowed
                if (arg.flow == "forward") {
                    state.previousFolders.push(state.selectedFolder)
                    state.selectedFolder = state.foldersList.filter(folder => folder.name == arg.name)[0]
                } else {
                    state.selectedFolder = state.previousFolders[state.previousFolders.length - 1]
                    state.previousFolders.splice(state.previousFolders.indexOf(arg.name), state.previousFolders.length - state.previousFolders.indexOf(arg.name)) //[1, 2, 3, 4, 5, 6, 7, 8, 9].splice(arr.indexOf(5),arr.length-arr.indexOf(5)) returns [5..9] and sets array to [1..4]
                }
                console.log(arg.name, arg.flow)
                if (arg.name == 'root') { state.selectedFolder = { name: 'root', id: 'appDataFolder' }; return } //handle root as setFolder

                console.log(state.selectedFolder.name, "hh", state.previousFolders)

            },
        },
        actions: {
            refreshFilesList({ commit, state }, name = null, mType = null, folder = state.selectedFolder.id) {
                var s = {
                        " ": "%20",
                        "=": "%3D",
                        ",": "%2C",
                        '"': "%22"
                    }
                    //try to simplify the following code later... future me sorry
                var fLink;
                folder != null ? fLink = "'" + folder + "'" + s[" "] + "in" + s[" "] + "parents" : fLink = "";

                var mLink;
                mType != null ? mLink = "mimeType" + s["="] + "'" + mType + "'" : mLink = "";

                var nLink;
                name != null ? nLink = "name" + s[" "] + "contains" + s[" "] + "'" + name + "'" : nLink = "";

                var and = s[" "] + "and" + s[" "];

                var link = "https://www.googleapis.com/drive/v3/files?spaces=appDataFolder&q=" + nLink; //complex turnary upcoming lol xb
                name != null ? (folder != null ? link += add : (mType != null ? link += and : link += "")) : link += ""
                link += fLink;
                mType != null ? (name != null ? link += and : (folder != null ? link += and : link += "")) : link += ""
                link += mLink

                console.log(link)
                var xhr_s = new XMLHttpRequest;

                xhr_s.open("GET", link);

                xhr_s.setRequestHeader('Authorization', 'Bearer ' + state.accessToken);

                xhr_s.onload = function() {
                    commit('setFilesList', JSON.parse(this.response).files.filter(file => file.mimeType != 'application/vnd.google-apps.folder'))
                    console.log(state.filesList)
                }

                xhr_s.send();

            },
            refreshFoldersList({ commit, state }, name = null, mType = 'application/vnd.google-apps.folder', folder = state.selectedFolder.id) {
                var s = {
                        " ": "%20",
                        "=": "%3D",
                        ",": "%2C",
                        '"': "%22"
                    }
                    //try to simplify the following code later... future me sorry
                var fLink;
                folder != null ? fLink = "'" + folder + "'" + s[" "] + "in" + s[" "] + "parents" : fLink = "";

                var mLink;
                mType != null ? mLink = "mimeType" + s["="] + "'" + mType + "'" : mLink = "";

                var nLink;
                name != null ? nLink = "name" + s[" "] + "contains" + s[" "] + "'" + name + "'" : nLink = "";

                var and = s[" "] + "and" + s[" "];

                var link = "https://www.googleapis.com/drive/v3/files?spaces=appDataFolder&q=" + nLink; //complex turnary upcoming lol xb
                name != null ? (folder != null ? link += add : (mType != null ? link += and : link += "")) : link += ""
                link += fLink;
                mType != null ? (name != null ? link += and : (folder != null ? link += and : link += "")) : link += ""
                link += mLink

                console.log(link)
                var xhr_s = new XMLHttpRequest;

                xhr_s.open("GET", link);

                xhr_s.setRequestHeader('Authorization', 'Bearer ' + state.accessToken);

                xhr_s.onload = function() {
                    commit('setFoldersList', JSON.parse(this.response).files)
                    console.log(state.foldersList)
                }

                xhr_s.send();

            },
        },
        modules: {}
    })
