<template>
  <div class="card">
    <header class="card-header">
      <p class="card-header-title">
        File Manager
      </p>
      <div class="card-header-icon">
        <div class="dropdown is-right" id="dropdown1">
          <div class="dropdown-trigger">
            <button
              class="button"
              aria-haspopup="true"
              aria-controls="dropdown-menu"
              
            >
            <div id="selected">
              <div class="selected">{{ selected }}</div>
            </div>
              <span class="icon is-small">
                <i class="fa fa-angle-down" aria-hidden="true"></i>
              </span>
            </button>
          </div>
          <div class="dropdown-menu" id="dropdown-menu" role="menu">
            <div class="dropdown-content">
              <div class="dropdown-item title is-6">Folders:</div>
              
              <a href="#" class="dropdown-item" v-for="folder in folders" :key="folder" @click.prevent="selectFolder(folder.name,'forward')">
                {{ folder.name }}
              </a>
              <a href="#" class="dropdown-item" v-if="isRoot" @click.prevent="selectFolder(lastFolder, 'back')"><b>Go back</b></a>
              <hr class="dropdown-divider">
              <div class="dropdown-item title is-6">Files:</div>
              <a href="#" class="dropdown-item" v-for="file in files" :key="file" @click.prevent="selectFile(file.name)">
                {{ file.name }}
              </a>
              
              
            </div>
          </div>
        </div>
      </div>
    </header>

    <div class="card-content">
      <nav class="breadcrumb is-small has-dot-separator" aria-label="breadcrumbs">
          <ul>
            <li v-for="folder in previousFolders" :key="folder"><a href="#"  @click.prevent="selectFolder(folder.name, back)">{{ folder.name }}</a></li>
            
            <li class="is-active"><a href="#" aria-current="page">{{ $store.state.selectedFolder.name}}</a></li>
          </ul>
        </nav>
      <div class="content">
        
        <div class="field has-addons">
              <div class="control has-icons-left is-expanded">
                <input class="input is-rounded is-success" type="text" placeholder="Text input" v-model="txt">
                  <span class="icon is-small is-left">
                  <i class="mdi mdi-message-outline"></i>
                  </span>
        
              </div>
              <div class="control">
              <a class="button is-info is-outlined is-rounded" @click="fileSelect">
                <input type="file" style="display: none" ref="file" id="file" @change="fileChange">
                <i class="fa fa-plus "></i>
              </a>
            </div>
            <div class="control">
              <Upload :uploadContent="upload" @complete="clear(1)" :slotUsed='true'>
                
                  <i class="fa fa-paper-plane"></i>
                
              </Upload>
            </div>
        </div>
      </div>
    </div>
    <footer class="card-footer">
      <Download class="card-footer-item" @complete="clear"/>
      <Upload cls="card-footer-item" :uploadContent="upload" @complete="clear(1)" :slotUsed='false'/> <!-- dont use :cls -->
      <Delete cls="card-footer-item" @complete="clear" :folder='false'>Delete</Delete>
      <Delete  v-if="isRoot" @complete="clear" :folder='true'>Delete Current Folder</Delete>
    </footer>
  </div>
  <span v-show="false">{{ refreshFiles }}</span>
</template>

<script>

import Delete from './actions/Delete';
import Download from './actions/Download';
import Upload from './actions/Upload';

export default {
    data(){
        return {
          //selected: 'Select Files'
          txt: '',
          fileInp: []
        }
    },
    mounted(){
      const dropdown = document.getElementById('dropdown1');
      const active = document.querySelector('.is-active')
      document.body.addEventListener('click', function () {
        if (active) {
          dropdown.classList.remove('is-active')
        }
      })
      dropdown.addEventListener('click', function (event) {
          event.stopPropagation();
          this.classList.toggle('is-active');
      });
    },
    computed: {
      files(){
        return this.$store.state.filesList
      },
      folders(){
        return this.$store.state.foldersList
      },
      previousFolders(){
        return this.$store.state.previousFolders
      },
      selected(){
        var file = this.$store.state.selectedFile
        var name;
        (file) ? name = file.name : name = 'Select File'
        return name
      },
      selectedFolder(){},
      upload(){
        return [this.txt, this.fileInp]
      },
      refreshFiles(){
        this.$store.state.selectedFolder
        console.log(80)
        if(this.$store.state.signInState) {this.$store.dispatch('refreshFilesList'); this.$store.dispatch('refreshFoldersList')}
      },
      isRoot(){
        return this.$store.state.previousFolders.length
      },
      lastFolder(){
        var prevFolders = this.$store.state.previousFolders
        return prevFolders[prevFolders.length - 1].name
      }
      
    },
    methods: {
      selectFile(arg){
        this.$store.commit('setSelectedFile', arg)
      },
      selectFolder(nme, flw){
        var arg = {
          name : nme,
          flow: flw
        }
        console.log(arg)
        this.$store.commit('setSelectedFolder', arg)
      },
      fileSelect(){
        document.getElementById('file').click()
      },
      fileChange(){
        this.fileInp = this.$refs.file.files
        console.log(this.fileInp)
      },
      clear(arg){
        if(arg){this.fileInp = []; this.txt = ''; return}
        this.$store.commit('setSelectedFile', '')
      }
    },
    components:{
      Delete, Download, Upload
    }
}
</script>

<style scoped>
.card {
  width: 75vw;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  
}
.selected{
  width: 75px; /*span doesnt work use div*/
  overflow: auto;
}
.dropdown{
  text-align: left;
}

.dropdown-menu{
  max-height: 25vh;
  overflow: auto;
}
.card-footer{
  max-width: 100%;
  overflow: auto;
}
</style>
